<template>
  <div class="hello">

    <div v-if="loading" style="font-size:5rem; color: gray;">...</div>
    <ul class="table">
      <li v-for="(user, idx) in usersRef" v-if="user.role=='user'">
        <span class="idx">{{ idx+1 }}</span>
        <span class="name">{{ user.name }}</span>
        <span class="age">{{ user.age }}</span>
        <span class="city">{{ user.city }}</span>
      </li>
    </ul>

    <button id="btnLogout" class="btn" @click="logOut">
      Log out
    </button>

  </div>
</template>


<style lang="scss">

ul.table {
  margin: 4rem auto 2rem auto;
  color: #34495e;
  font-size: 0.7rem;
  text-align: left;
  li {
    // display: table;
    display: flex;
    justify-content: center;
    width: 100%;
    border-radius: 2px;
    padding: 0.6rem 0;
    height: 1.6rem;
    // table-layout: fixed;
    // display: inline-block;
    span {
      align-self: center;
      // display: table-cell;
      // display: inline-block;
      // vertical-align: middle;
      padding-left: 0.6rem;
      line-height: 1.3;
      // width: 30%;
    }
    span.idx {width: 10px;}
    span.name {width: 100px;}
    span.age {width: 15px;}
    span.city {width: 80px;}
  }
  li:nth-child(odd) {
    background-color: #ecf0f1;
  }
}

</style>




<script>

// Firebase
import firebase from '../modules/firebase'
var VueFire = require('vuefire')
import Vue from 'vue'
Vue.use(VueFire)

var loading = true
var counter = 0

export default {
  name: 'dashboard',
  data () {
    return {
      data: this.$root.$data,
      loading: loading,
      counter: counter
    }
  },
  firebase: {
    usersRef: firebase.database().ref('events/2017-02-sorrento/users/')
  },
  methods: {
    logOut: function () {
      firebase.auth().signOut()
    },
    addOne: function () {
      this.counter++
      counter = this.counter
      console.log(counter)
    }
  },
  mounted: function () {
    firebase.database().ref('events/2017-02-sorrento/users/').once('value', snapshot => {
      this.loading = false
      this.caz = snapshot.val()
    })
  }
}
</script>



