<template>
  <div class="login">
    <!-- <h1>{{title}}</h1> -->

    <!-- email -->
    <input id="txtEmail" type="email" placeholder="Email" v-model.trim="email">
    <!-- password -->
    <input id="txtPassword" type="password" placeholder="Password" v-model.trim="password">

    <button id="btnLogin" class="btn action" @click="logIn" v-bind:class="{ 'hide': loginHide }">
      Log in
    </button>

    <button id="btnSignUp" class="btn secondary" @click="signUp" v-bind:class="{ 'hide': loginHide }">
      Sign Up
    </button>

    <button id="btnLogout" class="btn action" @click="logOut" v-bind:class="{ 'hide': logoutHide }">
      Log out
    </button>

  </div>
</template>

<script>

// Firebase
import firebase from '../modules/firebase'

export default {
  name: 'login',
  beforeCreate: function () {
    // Add a realtime listener
    firebase.auth().onAuthStateChanged(firebaseUser => {
      if (firebaseUser) {
        this.user = firebaseUser
        // console.log(firebaseUser)
        this.loginHide = true
        this.logoutHide = false
        // window.location = '/dashboard.html'
      } else {
        // console.log('not logged in')
        this.loginHide = false
        this.logoutHide = true
        // window.location = '/login.html'
      }
    })
  },
  data () {
    return {
      title: 'Login',
      loginHide: false,
      logoutHide: true,
      email: '',
      password: '',
      user: {} // firebase user
    }
  },
  methods: {
    logIn: function () {
      const auth = firebase.auth()
      const email = this.email
      const pass = this.password
      const promise = auth.signInWithEmailAndPassword(email, pass)
      promise.catch(e => console.log(e.message))
    },
    signUp: function () {
      console.log('sign up')
      const auth = firebase.auth()
      const email = this.email
      const pass = this.password
      const promise = auth.createUserWithEmailAndPassword(email, pass)
      promise.catch(e => console.log(e.message))
    },
    logOut: function () {
      firebase.auth().signOut()
    }
  }
}

</script>



